body {
  min-height: 100%;
  margin: 0;
}
// Убрала стили шрифта, т.к. он везде разный - удобнее прописывать свой для каждого блока, чтобы не забыть где какой.

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
